import "../styles/App.css";
import { Link } from "react-router-dom";
import { useEffect } from "react";
//
// import { NavigationContainer } from "@react-navigation/native";
// import { navigationRef } from "./RootNavigation";

function Nav() {
  const linkStyles = {
    cursor: "pointer",
    textDecoration: "none",
  };
  function scrollToTop() {
    window.scrollTo({ top: 0, behavior: "smooth" });
  }
  //
  // Initiate Functions
  useEffect(function NavLoad() {
    // Grab Elements

    const Nav_overlay = document.querySelector(".Nav_overlay"),
      Nav_Expand_Button = document.querySelector(".Nav_Expand_Button"),
      Pop_Up = document.querySelector(".Pop_Up"),
      Pop_Up_li = document.querySelectorAll(".Pop_Up li"),
      center_nav = document.querySelector(".center_nav"),
      right_nav = document.querySelector(".right_nav"),
      SearchInput = document.querySelector(".right_nav input"),
      SearchIcon = document.querySelector(".right_nav svg");

    // Functions
    function NavScroll() {
      if (window.scrollY > 0) {
        //
        Nav_overlay.style.background = `#151515`;
      } else if (window.scrollY === 0) {
        //
        Nav_overlay.style = "";
      }
    }
    window.addEventListener("scroll", NavScroll);
    //
    // Expand The Fucking POP_Up
    var isExpanded = false;

    //

    const Swap_PopUp = () => {
      isExpanded = !isExpanded;
      if (isExpanded) {
        //
        Pop_Up.classList.add("Pop_Up_Active");

        Nav_Expand_Button.classList.add("Nav_Expand_Button_Active");
        //
      } else if (!isExpanded) {
        //
        Pop_Up.classList.remove("Pop_Up_Active");

        Nav_Expand_Button.classList.remove("Nav_Expand_Button_Active");
        //
      }
    };
    //
    const Collapse_PopUp = () => {
      // Close The Fucking PopUp Manualy
      isExpanded = false;
      Pop_Up.classList.remove("Pop_Up_Active");

      Nav_Expand_Button.classList.remove("Nav_Expand_Button_Active");
    };
    // Run it
    Nav_Expand_Button.addEventListener("click", Swap_PopUp);

    for (let index = 0; index < Pop_Up_li.length; index++) {
      const element = Pop_Up_li[index];
      element.style.transition = `all 0.8s ${
        index * 0.05
      }s cubic-bezier(0.25, 0, 0, 1)`;

      element.addEventListener("click", scrollToTop);
      if (index === Pop_Up_li.length - 1) {
        break;
      }
    }
    Pop_Up.addEventListener("click", Collapse_PopUp);
    // Search Bar
    function SearchFocusIn() {
      center_nav.style.transform = `translateX(-100px)`;
      center_nav.style.width = "0";
      center_nav.style.opacity = `0`;

      right_nav.style.opacity = `1`;

      SearchIcon.style.transform = `rotate(90deg)`;
    }
    function SearchFocusOut() {
      if (SearchInput.value.length === 0) {
        center_nav.style = ``;
        right_nav.style = ``;
        SearchIcon.style = ``;
      }
    }
    //
    //
    //
    center_nav.addEventListener("click", scrollToTop);
    SearchInput.addEventListener("focusin", SearchFocusIn);
    SearchInput.addEventListener("focusout", SearchFocusOut);
  }, []);

  return (
    <nav className="nav_container">
      <div className="Nav_overlay"></div>
      <div className="Pop_Up">
        <ul>
          <Link to="/Tin-moi">
            <li>Tin mới</li>
          </Link>
          <Link to="/Thoi-trang-ao">
            <li>Thời trang ảo</li>
          </Link>
          <Link to="/Virtual-influencer">
            <li>Virtual Influencer</li>
          </Link>
          <Link to="/Tai-Viet-Nam">
            <li>Tại Việt Nam</li>
          </Link>
          <Link to="/Ve-chung-toi">
            <li>Về chúng tôi</li>
          </Link>
          <Link to="/Lien-he">
            <li>Liên hệ</li>
          </Link>
        </ul>
        <h5 id="version">v1.0 Alpha</h5>
      </div>
      <div className="main_nav">
        <div className="left_nav">
          <div className="Nav_Expand_Button">
            <div></div>
            <div></div>
          </div>
        </div>
        <div className="center_nav">
          <Link to="/" style={linkStyles}>
            <div className="logo">
              <h1 className="logo_main">VDFM</h1>
              {/* <h2 className="logo_des">Vietnam's Digital Fashion Magazine</h2> */}
            </div>
          </Link>
        </div>
        <div className="right_nav">
          <div className="Search_bar">
            <input
              type="text"
              name="search"
              required
              spellCheck="false"
              placeholder="Tìm gì đó"
              autoComplete="off"
            />
            <svg
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 936 936"
              id="search_icon"
            >
              <rect className="srch-1" width="936" height="936" />
              <circle className="srch-2" cx="436.25" cy="436.25" r="174.5" />
              <line
                className="srch-2"
                x1="674.25"
                y1="674.25"
                x2="559.63"
                y2="559.63"
              />
            </svg>
          </div>
        </div>
      </div>
    </nav>
  );
}

export default Nav;
